import Alpine from 'alpinejs'

// import "./style/admin.css" 
window.Alpine = Alpine


const App = {
    state: {
        tab: 'general',
    },
    options: ajaxpress_admin.options,

    get buttonText(){
        return this.state.saving ? 'Saving...' : this.state.saved ? 'Saved!' : 'Save Changes'
    },

    init(){ 
        
        this.InitRouter()
    },
    InitRouter(){ 
        const tab = window.location.hash.replace('#', '')
        App.state.tab = tab || 'general'
    },
    get error(){
        // check target
        const target = this.options.target || ''
        
        if(!target){
            return 'Target element is required'
        }

        // check target is a valid selector using regex
        let regex = /^([a-zA-Z][\w-]*|\#[a-zA-Z][\w-]*|\.[a-zA-Z][\w-]*|\[[^\]]+\]|:[a-zA-Z-]+)(?:\s*[>+\s]\s*([a-zA-Z][\w-]*|\#[a-zA-Z][\w-]*|\.[a-zA-Z][\w-]*|\[[^\]]+\]|:[a-zA-Z-]+))*$/;

        if(!regex.test(target)){
            return 'Target element is not a valid selector'
        }

        // check excludes are valid selectors, but it's not required
        if (this.options.excludes) {
            const excludes = this.options.excludes.split(',')
            for (let i = 0; i < excludes.length; i++) {
                const exclude = excludes[i].trim()
                if(!regex.test(exclude)){
                    return 'Excludes element is not a valid selector'
                }
            }
        }


        return false

    },
    async Submit(){  

        if( false !== this.error ) return

        this.state.saving = true

        const formData = new URLSearchParams();
        formData.append('action', 'ajaxpress_save_settings');
        formData.append('nonce', ajaxpress_admin.nonce);

        Object.keys(this.options).forEach(key => {
            let value = this.options[key]
            value = typeof value === 'boolean' ? value ? 1 : 0 : value
            formData.append(key, value);
        })

        const url = (ajaxpress_admin.ajax_url + '?action=ajaxpress_save_settings&nonce=' + ajaxpress_admin.nonce)
        const response = await fetch(url, {
            method: 'POST',
            body: formData,
            headers: {
                'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
            }
        })

        const data = await response.json()

        this.state.saving = false
        this.state.saved = true

        setTimeout(() => {
            this.state.saved = false
        }, 2000)

        console.log("response", data);
    }
}

Alpine.data('ajaxpress', () => App)
Alpine.start()